@import "https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap";
@import "https://fonts.googleapis.com/css2?family=Rajdhani:wght@500&display=swap";
@import "https://fonts.googleapis.com/css2?family=VT323&display=swap";
@import "https://fonts.googleapis.com/css2?family=Saira+Condensed:wght@500&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, Segoe UI, Arial, sans-serif;
}

html, body {
  width: 100%;
  height: 100%;
  line-height: 1.5;
}

#screen.fullscreen::-webkit-media-controls-panel {
  display: none !important;
}

#screen.fullscreen::-moz-media-controls {
  display: none !important;
}

button, input, select, label {
  font-size: inherit;
  color: inherit;
  background: none;
  border: none;
  outline: none;
}

:root {
  --header-height: 60px;
  --control-width: 320px;
  --video-max-base-width: 720px;
  --transition: all .3s ease-in-out;
  --shadow: 0 2px 4px #0000001a;
  --panel-border-radius: 12px;
  --panel-shadow: 0 4px 12px #00000026;
  --slider-track-height: 4px;
  --slider-thumb-size: 16px;
  --taskbar-height: 40px;
  --theme-toggle-width: 50px;
  --theme-toggle-height: 26px;
  --theme-toggle-padding: 3px;
  --theme-toggle-knob-size: calc(var(--theme-toggle-height)  - 2 * var(--theme-toggle-padding));
}

[data-theme="dark"] {
  --bg-color: #121212;
  --text-color: #f5f5f5;
  --header-bg: #1e1e1e;
  --header-text: #ff9800;
  --control-bg: #2a2a2a;
  --input-bg: #1c1c1c;
  --button-bg: #ff9800;
  --button-text: #fff;
  --button-hover: #fb8c00;
  --placeholder-bg: #2a2a2a;
  --video-bg: #1e1e1e;
  --taskbar-bg-color: #212121f2;
  --taskbar-button-hover-bg: #ffffff0d;
  --taskbar-icon-color: #f5f5f5;
  --panel-bg-color: #1e1e1e;
  --panel-text-color: #f5f5f5;
  --panel-secondary-text-color: #ffcc80;
  --panel-icon-bg-inactive: #2a2a2a;
  --panel-icon-color-inactive: #ffcc80;
  --panel-icon-bg-active: #ff9800;
  --panel-icon-color-active: #fff;
  --slider-track-color: #2a2a2a;
  --slider-track-active-color: #ff9800;
  --slider-thumb-color: #ff9800;
  --app-drawer-text-color: #f5f5f5;
  --app-drawer-icon-bg: #2a2a2a;
  --theme-toggle-bg: linear-gradient(180deg, #2a2a2a 0%, #1e1e1e 100%);
  --theme-toggle-knob-color: #ff9800;
  --theme-toggle-border-color: #ffcc80;
  --checkbox-bg-active: #ff9800;
  --checkbox-border-active: #fb8c00;
  --checkbox-text-active: #fff;
  --rotate-button-bg: #ff9800;
  --rotate-button-hover-bg: #fb8c00;
  --label-disabled-color: #888;
}

[data-theme="light"] {
  --bg-color: #ffe5b4;
  --text-color: #2e2e2e;
  --header-bg: #fc8d05;
  --header-text: black;
  --control-bg: #ffb74d;
  --input-bg: #ffcc80;
  --button-bg: #e65100;
  --button-text: #fff;
  --button-hover: #bf360c;
  --placeholder-bg: #ffcc80;
  --video-bg: #ffe5b4;
  --taskbar-bg-color: #ffcc80e6;
  --taskbar-button-hover-bg: #00000012;
  --taskbar-icon-color: #2e2e2e;
  --panel-bg-color: #ffcc80;
  --panel-text-color: #2e2e2e;
  --panel-secondary-text-color: #4e342e;
  --panel-icon-bg-inactive: #ffb74d;
  --panel-icon-color-inactive: #4e342e;
  --panel-icon-bg-active: #e65100;
  --panel-icon-color-active: #fff;
  --slider-track-color: #ffb74d;
  --slider-track-active-color: #e65100;
  --slider-thumb-color: #e65100;
  --app-drawer-text-color: #2e2e2e;
  --app-drawer-icon-bg: #ffb74d;
  --theme-toggle-bg: linear-gradient(180deg, #ffcc80 0%, #ffb74d 100%);
  --theme-toggle-knob-color: #e65100;
  --theme-toggle-border-color: #ff9800;
  --checkbox-bg-active: #e65100;
  --checkbox-border-active: #bf360c;
  --checkbox-text-active: #fff;
  --rotate-button-bg: #e65100;
  --rotate-button-hover-bg: #bf360c;
  --label-disabled-color: #8d6e63;
}

.main-contentt {
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 80vh;
  display: flex;
}

body {
  background: var(--bg-color);
  color: var(--text-color);
  overscroll-behavior: none;
  flex-direction: column;
  min-height: 100vh;
  transition: background-color .3s, color .3s;
  display: flex;
}

header.hidden {
  box-shadow: none;
  transform: translateY(-100%);
}

header {
  background-color: var(--header-bg);
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: relative;
}

.header-right {
  align-items: center;
  gap: 1rem;
  display: flex;
}

header h1 {
  color: var(--header-text);
  text-align: center;
  flex: 1;
  margin: 0;
  font-family: Orbitron, sans-serif;
  font-size: 2.5rem;
}

.theme-toggle {
  background: var(--theme-toggle-bg, #444);
  border: 1px solid var(--theme-toggle-border-color, #666);
  box-sizing: border-box;
  border-radius: 40px;
  justify-content: flex-start;
  align-items: center;
  width: 72px;
  height: 32px;
  padding: 4px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}

.theme-toggle-knob {
  background: var(--theme-toggle-knob-color, #fff);
  border-radius: 50%;
  width: 24px;
  height: 24px;
  transition: left .3s;
  position: absolute;
  top: 4px;
  left: 4px;
}

.theme-toggle[aria-checked="true"] .theme-toggle-knob {
  left: 44px;
}

.app-container {
  width: 100%;
  height: calc(100vh - var(--header-height));
  flex: 1;
  display: flex;
}

.sidebar {
  width: var(--control-width);
  background: var(--control-bg);
  box-shadow: var(--shadow);
  z-index: 10;
  flex-direction: column;
  gap: .75rem;
  padding: 1rem;
  display: flex;
  overflow-y: auto;
}

.main-content {
  flex: 1;
  justify-content: center;
  align-items: center;
  margin-right: 330px;
  padding: 1.5rem;
  display: flex;
}

#streamArea {
  width: 100%;
  max-width: var(--video-max-base-width);
  max-height: calc(100vh - var(--header-height)  - 3rem);
  aspect-ratio: 9 / 16;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 16px #0000001a;
}

#videoBorder {
  pointer-events: none;
  z-index: 5;
  transition: top .1s linear, left .1s linear, width .1s linear, height .1s linear;
  display: none;
  position: absolute;
}

#screen {
  object-fit: contain;
  background: var(--video-bg);
  opacity: 0;
  width: 100%;
  height: 100%;
  transition: opacity .3s;
}

.device-card {
  color: #fff;
  background: linear-gradient(#222 80%, #181818 100%);
  border: 4px solid #222;
  border-radius: 38px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 320px;
  min-width: 320px;
  max-width: 320px;
  height: 640px;
  margin: 0 .5rem;
  padding: 1.5rem 1rem 2.5rem;
  font-family: Orbitron, sans-serif;
  font-size: 1.1rem;
  transition: transform .3s, box-shadow .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 12px 36px #222, 0 2px 24px #00000040;
}

.device-card:before {
  content: "";
  opacity: .7;
  background: #444;
  border-radius: 4px;
  width: 60px;
  height: 8px;
  display: block;
  position: absolute;
  top: 18px;
  left: 50%;
  transform: translateX(-50%);
}

.device-card:after {
  content: "";
  opacity: .7;
  background: #222;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  display: block;
  position: absolute;
  bottom: 22px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2px 8px #0005;
}

.device-card h3 {
  color: #fff;
  text-align: center;
  margin-top: 2.8rem;
  margin-bottom: 2.5rem;
  font-family: Orbitron, sans-serif;
  font-size: 1.3rem;
}

.device-card p {
  color: #eee;
  text-align: center;
  margin-bottom: 1.3rem;
  font-size: 1.05rem;
  line-height: 2;
}

.device-card .buttons {
  justify-content: center;
  width: 100%;
  margin-top: 1.5rem;
  display: flex;
}

.device-card .neon-button {
  color: #fff;
  cursor: pointer;
  background: #ff9800;
  border: none;
  border-radius: 8px;
  padding: .5rem 1.2rem;
  font-size: 1rem;
  transition: background .2s;
  box-shadow: 0 0 8px #ff9800;
}

.device-card .neon-button:hover {
  background: #fb8c00;
}

.device-card h3, .device-card p, .device-card strong {
  color: #fff !important;
}

#screen.visible {
  opacity: 1;
}

#broadwayCanvas {
  opacity: 0;
  transition: opacity .3s;
}

#broadwayCanvas.visible {
  opacity: 1;
}

#screen.control-enabled {
  cursor: crosshair;
}

#videoPlaceholder {
  background: var(--placeholder-bg);
  width: 100%;
  height: 100%;
  color: var(--text-color);
  z-index: 10;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  font-family: VT323, monospace;
  font-size: 2rem;
  transition: opacity .3s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#videoPlaceholder.hidden {
  opacity: 0;
}

.controls {
  background-color: var(--panel-bg-color);
  color: var(--panel-text-color);
  box-shadow: var(--panel-shadow);
  transition: var(--transition);
  border: 2px solid #ff9800;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  padding: 1rem;
}

.card.display-settings h3 {
  color: var(--header-text);
  margin-top: 0;
  font-size: 1.2rem;
}

.card.display-settings {
  background-color: var(--panel-bg-color);
  color: var(--panel-text-color);
  box-shadow: var(--panel-shadow);
  transition: var(--transition);
  border: 2px solid #ff9800;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  padding: 1rem;
}

.card.display-settings select {
  background-color: var(--input-bg);
  color: var(--text-color);
  appearance: none;
  border: 2px solid #ff9800;
  border-radius: 6px;
  width: 100%;
  margin-bottom: 1rem;
  padding: .5rem;
  font-family: Orbitron, sans-serif;
  font-size: 1rem;
  transition: all .3s;
  box-shadow: 0 0 10px #ff9800;
}

.card.display-settings select:focus {
  border-color: #ff9800;
  outline: none;
  box-shadow: 0 0 20px #ff9800;
}

.card.display-settings select option {
  background-color: var(--input-bg);
  color: var(--text-color);
}

.card.display-settings select option:hover {
  color: #fff;
  background-color: #ff9800;
}

.card.display-config {
  background-color: var(--panel-bg-color);
  color: var(--panel-text-color);
  box-shadow: var(--panel-shadow);
  transition: var(--transition);
  border: 2px solid #ff9800;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  padding: 1rem;
}

.card.display-config h3 {
  color: var(--header-text);
  margin-top: 0;
  font-size: 1.2rem;
}

.card.display-config label {
  color: var(--text-color);
  margin-bottom: .5rem;
  display: block;
}

.checkbox-group.display-mode-group {
  flex-direction: column;
  gap: .5rem;
  margin-top: .5rem;
  display: flex;
}

.checkbox-group input[type="checkbox"] {
  appearance: none;
  border: 2px solid var(--checkbox-border-active);
  cursor: pointer;
  background-color: #0000;
  border-radius: 4px;
  width: 18px;
  height: 18px;
  transition: background-color .2s;
  position: relative;
}

.card.additional-options {
  background-color: var(--panel-bg-color);
  color: var(--panel-text-color);
  box-shadow: var(--panel-shadow);
  transition: var(--transition);
  border: 2px solid #ff9800;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  padding: 1rem;
}

.card.additional-options h3 {
  color: var(--header-text);
  margin-top: 0;
  font-size: 1.2rem;
}

.card.additional-options label {
  color: var(--text-color);
  margin-bottom: .5rem;
  display: block;
}

.checkbox-group {
  flex-direction: column;
  gap: .5rem;
  margin-top: .5rem;
  display: flex;
}

.checkbox-group input[type="checkbox"] {
  appearance: none;
  border: 2px solid var(--checkbox-border-active);
  cursor: pointer;
  vertical-align: middle;
  background-color: #0000;
  border-radius: 4px;
  width: 18px;
  height: 18px;
  transition: background-color .2s;
  position: relative;
}

.checkbox-group input[type="checkbox"]:checked {
  background-color: var(--checkbox-bg-active);
}

.checkbox-group input[type="checkbox"]:checked:after {
  content: "";
  border: solid var(--checkbox-text-active);
  border-width: 0 2px 2px 0;
  width: 4px;
  height: 8px;
  position: absolute;
  top: 2px;
  left: 6px;
  transform: rotate(45deg);
}

.checkbox-group label {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 2px solid #ff9800;
  border-radius: 8px;
  margin: .5rem;
  padding: .5rem 1rem;
  transition: all .3s;
  display: inline-block;
  box-shadow: 0 0 10px #ff9800;
}

.checkbox-group input[type="checkbox"] {
  display: none;
}

.home-icon {
  color: #ffd18d;
  z-index: 1000;
  font-size: 1.8rem;
  text-decoration: none;
  transition: transform .2s, color .3s;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.home-icon:hover {
  color: #ffd18d;
  transform: scale(1.2);
}

.card.performance-settings h3 {
  color: var(--header-text);
  margin-top: 0;
  font-size: 1.2rem;
}

.card.performance-settings {
  background-color: var(--panel-bg-color);
  color: var(--panel-text-color);
  box-shadow: var(--panel-shadow);
  transition: var(--transition);
  border: 2px solid #ff9800;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  padding: 1rem;
}

.card.performance-settings select {
  background-color: var(--input-bg);
  color: var(--text-color);
  appearance: none;
  border: 2px solid #ff9800;
  border-radius: 6px;
  width: 100%;
  margin-bottom: 1rem;
  padding: .5rem;
  font-family: Orbitron, sans-serif;
  font-size: 1rem;
  transition: all .3s;
  box-shadow: 0 0 10px #ff9800;
}

.card.performance-settings select:focus {
  border-color: #ff9800;
  outline: none;
  box-shadow: 0 0 20px #ff9800;
}

.card.performance-settings select option, .card.performance-settings select option:hover {
  background-color: var(--input-bg);
  color: var(--text-color);
}

.card.performance-settings input[type="number"] {
  background-color: var(--input-bg);
  color: var(--text-color);
  border: 2px solid #ff9800;
  border-radius: 6px;
  width: 100%;
  margin-bottom: 1rem;
  padding: .5rem;
  font-family: Orbitron, sans-serif;
  font-size: 1rem;
  transition: all .3s;
  box-shadow: 0 0 10px #ff9800;
}

.card.performance-settings input[type="number"]:focus {
  background-color: var(--input-bg);
  color: #fff;
  box-shadow: 0 0 20px #ff9800, inset 0 0 30px #ff9800;
}

.card {
  background: var(--control-bg);
  border-radius: var(--panel-border-radius);
  box-shadow: var(--panel-shadow);
  padding: 1rem;
}

.card h3 {
  color: var(--text-color);
  margin-bottom: .75rem;
  font-size: 1.1rem;
}

.card label {
  color: var(--text-color);
  margin: .5rem 0 .25rem;
  font-size: .95rem;
  display: block;
}

.card label.disabled {
  color: var(--label-disabled-color);
  cursor: not-allowed;
}

.card select, .card input[type="text"], .card input[type="number"] {
  background: var(--input-bg);
  width: 100%;
  color: var(--text-color);
  border: 1px solid #0000;
  border-radius: 6px;
  padding: .6rem;
  font-size: .95rem;
}

.card select:disabled, .card input[type="text"]:disabled, .card input[type="number"]:disabled {
  opacity: .7;
  cursor: not-allowed;
  background-color: var(--placeholder-bg);
}

.card select {
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-position: right .75rem center;
  background-repeat: no-repeat;
  background-size: 1rem;
  margin-bottom: .5rem;
  padding-right: 2rem;
}

.card input[type="checkbox"] {
  display: none;
}

.card small {
  color: var(--panel-secondary-text-color);
  margin: .25rem 0;
  font-size: .85rem;
  display: block;
}

.checkbox-group {
  flex-wrap: wrap;
  gap: .5rem;
  margin: .5rem 0;
  font-family: Orbitron, sans-serif;
  display: flex;
}

.checkbox-group label {
  background: var(--input-bg);
  color: #000;
  cursor: pointer;
  text-align: center;
  border: 1px solid #0000;
  border-radius: 8px;
  flex: auto;
  min-width: 80px;
  padding: .5rem .75rem;
  font-family: Orbitron, sans-serif;
  font-size: .9rem;
  transition: all .2s;
}

.checkbox-group input[type="checkbox"]:checked + label {
  background: var(--checkbox-bg-active);
  color: var(--checkbox-text-active);
  border-color: var(--checkbox-border-active);
}

.checkbox-group label:hover:not(.disabled-label) {
  background: var(--taskbar-button-hover-bg);
}

.checkbox-group input[type="checkbox"]:disabled + label, .checkbox-group label.disabled-label {
  opacity: .6;
  cursor: not-allowed;
  background-color: var(--placeholder-bg);
  color: var(--label-disabled-color);
}

.checkbox-group {
  border: 2px solid #ff9800;
  border-radius: 8px;
  flex-direction: column;
  gap: .75rem;
  max-width: 500px;
  margin: 1rem auto;
  padding: 1rem;
  font-family: Orbitron, sans-serif;
  display: flex;
  box-shadow: 0 0 15px #ff9800;
}

.checkbox-group input[type="checkbox"] {
  appearance: none;
  cursor: pointer;
  vertical-align: middle;
  background-color: #000;
  border: 2px solid #ff9800;
  border-radius: 4px;
  width: 20px;
  height: 20px;
  margin-right: .5rem;
  position: relative;
  box-shadow: inset 0 0 10px #ff9800;
}

.checkbox-group input[type="checkbox"]:checked:before {
  content: "✔";
  color: #000;
  text-align: center;
  background-color: #ff9800;
  border-radius: 2px;
  font-size: 14px;
  line-height: 20px;
  display: block;
}

.checkbox-group label:hover {
  color: #000;
  background-color: #ff9800;
}

.checkbox-group input[type="checkbox"]:checked + label {
  color: #fff;
  background-color: #ff9800;
}

#logArea {
  top: 1.7 rem;
  z-index: 1000;
  width: 300px;
  height: 100vh;
  color: var(--text-color);
  border-left: 2px solid #ff9800;
  flex-direction: column;
  padding: 1rem;
  font-family: Orbitron, sans-serif;
  display: flex;
  position: fixed;
  right: 0;
}

#toggleLogBtn {
  color: #ff9800;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .5px;
  background-color: #0000;
  border: 2px solid #ff9800;
  padding: .5rem 1rem;
  font-size: 1rem;
  transition: all .3s;
  box-shadow: 0 0 10px #ff9800, inset 0 0 20px #ff9800;
}

#toggleLogBtn:hover {
  color: #000;
  background-color: #ff9800;
  box-shadow: 0 0 20px #ff9800, inset 0 0 30px #ff9800;
}

#logContent {
  color: var(--text-color);
  border: 1px solid #ff9800;
  border-radius: 4px;
  flex: 1;
  margin-top: 1rem;
  padding: .5rem;
  font-size: .9rem;
  overflow-y: auto;
}

.landing-footer {
  text-align: center;
  color: #888;
  letter-spacing: .5px;
  background: none;
  width: 100vw;
  padding: 1.2rem 0;
  font-family: Orbitron, sans-serif;
  font-size: 1.05rem;
}

.buttons {
  flex-wrap: wrap;
  justify-content: center;
  gap: .5rem;
  display: flex;
}

.buttons .neon-button {
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .5px;
  min-width: 90px;
  max-width: 200px;
  color: var(--text-color);
  background: none;
  border: 2px solid #ff9800;
  border-radius: 6px;
  flex: auto;
