.device-grid {
  box-sizing: border-box;
  flex-flow: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  width: 100vw;
  min-height: 60vh;
  padding: 3rem 4vw;
  display: flex;
  background: none !important;
}

.device {
  color: #fff;
  text-align: center;
  background: #111;
  border: 2px solid #ff9800;
  border-radius: 30px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 500px;
  padding: .5rem;
  font-size: .75rem;
  transition: transform .2s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #ff9800, inset 0 0 20px #ff9800, 0 0 30px #ff980033;
}

.device:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px #ff9800, inset 0 0 25px #ff9800, 0 0 40px #ff980066;
}

.device:before {
  content: "";
  background: #555;
  border-radius: 2px;
  width: 40px;
  height: 4px;
  position: absolute;
  top: 6px;
  left: 50%;
  transform: translateX(-50%);
}

.device:after {
  content: "";
  background: #333;
  border: 2px solid #555;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: block;
  position: absolute;
  bottom: 8px;
  left: 50%;
  transform: translateX(-50%);
}

.main-contentt {
  z-index: 1;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  padding: 2rem;
  display: flex;
  position: relative;
}

.main-contentt:before {
  content: "";
  filter: blur(8px);
  z-index: -1;
  opacity: .8;
  background: url("viasat.f0c302b9.png") center / cover no-repeat;
  position: absolute;
  inset: 0;
}

.main-contentt:after {
  content: "";
  z-index: -1;
  background-color: #0006;
  position: absolute;
  inset: 0;
}

.grid-header {
